.sr-only {
    border: 0 !important;
    clip: rect(1px, 1px, 1px, 1px) !important; /* 1 */
    -webkit-clip-path: inset(50%) !important;
    clip-path: inset(50%) !important; /* 2 */
    height: 1px !important;
    margin: -1px !important;
    overflow: hidden !important;
    padding: 0 !important;
    position: absolute !important;
    width: 1px !important;
    white-space: nowrap !important; /* 3 */
}

.main-nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 20px;
}

.main-nav a {
    font-weight: bold;
    color: #2c3e50;
}

.main-nav a.router-link-exact-active {
    color: #42b983;
    text-align: center;
}

.main-nav-item {
    text-decoration: none;
    margin-right: 0.5rem;
    color: #00BC77;
}

.main-nav-item .fa-user-circle{
    margin-right: 7px;
    color: #2c3e50;
    font-size: 26px;
} 
.main-nav-item:hover {
    text-decoration: underline;
}

.main-nav-logo {
    display: flex;
    align-items: center;
}

.main-nav-logo-image {
    max-width: 100%;
    width: 200px;
}

.main-nav .infos-user {
    display: flex;
    align-items: center;
    gap: 40px;
}

.main-nav .infos-user-username {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 14px;
    color: #00BC77;
}

.main-nav .infos-user .fa {
    font-size: 26px;
    color: #00BC77;
}

.main-nav p {
    font-size: 18px;
}
.footer {
    display: flex;
    justify-content: center;
    border-top: 2px solid #ccc;
    padding: 2rem 0 1.5rem;
}

.footer-text {
    margin: 0;
    padding: 0;
}

@media (max-width: 720px) {
    .main-nav .infos-user {
        gap: 20px;
    }
    .main-nav .infos-user .fa {
        font-size: 18px;
    }
}